  padding: 2rem;
  margin: 0 auto;
  transform: translateY(-30rem);
  transition-property: all;
  transition-duration: 500ms;
  transition-timing-function: ease-in-out;
  opacity: 0;
  border-radius: 1rem;
}
.modal .formulario legend {
  font-size: 2rem;
