  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
.modal .formulario .submit-nuevo-menu.btneliminar {
  background-color: red;
}
@media (min-width: 768px) {
  .modal .formulario .submit-nuevo-menu {
    width: auto;
  }
}
.modal .formulario .submit-nuevo-menu:hover {
  background-color: #284b61;
  cursor: pointer;
}
