  background-color: #cc0000;
  padding: 1.5rem 4rem;
  color: white;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin-top: 10rem;
  display: block;
  margin: 5rem auto 0;
  border-radius: 0.5rem;
}
.modal .formulario .cerrar-modal.btneliminar {
  background-color: red;
}
@media (min-width: 768px) {
